.block__container {
    padding: 20px;
    border: 7px solid #C89AFC;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.photo {
    height: 370px;
    width: 350px;
    margin-bottom: 20px;
}

.head {
    font-size: 24px;
    font-weight: 600;
    letter-spacing: 0.5px;
}

.list__ul {
    list-style: none;
    margin: 20px 0;
}

.list__item {
    color: #fff;
    text-align: center;
}

.price {
    font-size: 18px;
    font-weight: 500;
}

.btn {
    color: #FFFFFF;
    background: linear-gradient(94.26deg, #C89AFC 9.51%, #7C6AFA 90.23%);
    border-radius: 50px;
    border: none;
    padding: 10px 30px;
    margin-top: 10px;
    cursor: pointer;
}

@media screen and (max-width: 400px) {
    .block__container {
        width: 280px;
    }
}